<template>
    <v-container>
      <v-row justify="center" class="space">
   <v-col
   justify="center"
        cols="12"
        md="4"
    v-for="item in 6"
        :key="item.id"
        :hidden="skeleton">
       <v-skeleton-loader
      class="mx-auto"
      max-width="300"
      type="card,actions"
      
       
    ></v-skeleton-loader></v-col></v-row>
    <v-row justify="center" class="space">
      
      <v-spacer></v-spacer>
      
      <v-col
        justify="center"
        cols="12"
        md="3"
        v-for="item in request"
        :key="item.id"
        
      ><v-card
      class="mx-auto"
      max-width="344"
    >
     
  
      <v-card-title >
        {{ item.cutomer_name }}
 </v-card-title>
  
      <v-card-subtitle >

        {{ item.package_name }}       </v-card-subtitle>
  
      <v-card-actions>
        <v-btn
          color="orange-lighten-2"
          variant="text"
          @click=" ShowRequestData(item.id) "
         
        >
          Explore
        </v-btn>
  
       
      </v-card-actions>
  
      <v-expand-transition>
        <div v-show="show">
          <v-divider></v-divider>
  
          <v-img
      :src="`${item.recipt_photo}`"
        height="600px"
        cover
      ></v-img>
        </div>
      </v-expand-transition>
    </v-card>
      </v-col>

      <v-spacer></v-spacer>

      <v-dialog v-model="Check_request" max-width="600px">
      
          <v-card
              class="mx-auto"
              max-width="344"
            >
              <v-img
                height="400px"
                width="500px"
                cover
              ></v-img>

              <v-card-title>
                {{request[check_id].cutomer_name}}
              </v-card-title>

              <v-card-subtitle>
                {{request[check_id].package_name}}
                            </v-card-subtitle>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="edit" text @click="ShareUnit = false">Cancel</v-btn>
            <v-btn color="edit" text @click="shareconfirm()">OK</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
            
    </v-row>
</v-container>
  </template>
  <script>
  export default {
    data: () => ({
      skeleton:true,
      Check_request:false,
      check_id:0,
    request:[
    {   id:0,
        cutomer_name:"aeham",
        type:"upgrade",
        package_name:"student package",
        price:"6$",
        recipt_photo:"https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
    },
    {   id:1,
        cutomer_name:'mhamad',
        type:'upgrade',
        package_name:"student package",
        price:"6$",
        recipt_photo:"https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"

    },
    {   id:2,
        cutomer_name:'ahmad',
        type:'upgrade',
        package_name:"student package",
        price:"6$",
        recipt_photo:"https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"


    },

    ]


}),
methods: {
 
    ShowRequestData(item) {
      this.Check_request=!this.Check_request 
      this.check_id=item
    
    },
  
  
  
  }


  }
  </script>