      mini:true,
      menu: false,}
   },
   
 methods: {
  Mini_SideBar() {
      this.$store.commit("Mini_SideBar");
    }
  ,
    Logout() {
     
      axios.post(`/api/logout`, {}).catch(error => console.log(error))
      localStorage.removeItem('token')
      this.$router.push('/login')
    }
  }
}
