<template>
  <v-container>
    <v-card>
      <v-container>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-list >
      <v-row>
        <v-col>
    <v-text-field v-model="Package_Name" label="Package Name" required></v-text-field>
    </v-col><v-col>
    <v-text-field v-model="space" label="Space" required></v-text-field>  </v-col>
    </v-row><v-row><v-col>
    <v-text-field v-model="Bandwidth" label="Bandwidth" required></v-text-field></v-col><v-col>
    <v-text-field v-model="Email_Accounts" label="E-mail Accounts" required></v-text-field></v-col>
    </v-row><v-row><v-col>
    <v-text-field v-model="MySQL_Accounts" label="MySQL Accounts" required></v-text-field></v-col><v-col>
    <v-text-field v-model="php_enable" label="php Enable" required></v-text-field></v-col>
  </v-row>
  <v-row><v-col>
      <v-text-field v-model="ssl_certificate" label="SSL certificate" required></v-text-field></v-col><v-col>
      <v-text-field v-model="Duration" label="Duration" required></v-text-field></v-col>
    </v-row>
  <v-row><v-col>
      <v-text-field v-model="Yearly_Price" label="Yearly Price" required></v-text-field></v-col><v-col>
      <v-text-field v-model="Yearly_price_Outside_Syria" label="Yearly price Outside Syria" required></v-text-field></v-col>
    </v-row>
     <div class="text-center my-4" >
                <v-btn
                  rounded
                  color="secondary"
                  dark
                  x-large
                  style="font-size: large;"
                  width="250px"
                >
                  Create
                </v-btn>
              </div>
    </v-list>
  </v-form>
  </v-container>  
  </v-card>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    sendData: {
      Package_Name: '',
      space: '',
      Bandwidth: '',
      Email_Accounts: '',
      MySQL_Accounts: '',
      php_enable: '',
      ssl_certificate: '',
      Duration: '',
      Yearly_Price: '',
      Yearly_price_Outside_Syria:''
      
    }



  }),
  methods: {

    savePackage() {
      this.loading = true,
        axios
          .post(
            '/api/questionAdd',
            {
              package_type: this.sendData.Package_Name,
              space: this.sendData.space,
              bandwidth: this.sendData.Bandwidth,
              email_accounts: this.sendData.Email_Accounts,
              mysql_accounts: this.sendData.MySQL_Accounts,
              php_enable: this.sendData.php_enable,
              ssl_certificate: this.sendData.ssl_certificate,
              duration: this.sendData.Duration,
              yearly_price: this.sendData.Yearly_Price,
              yearly_price_outside_syria: this.sendData.Yearly_price_Outside_Syria,
              available:true
            },
            {
              headers: {
                'Content-Type': `multipart/form-data`
              }
            }
          )
          .then(res => {
            console.log(res)
            this.loading = false
            this.done = true
          })
          .catch(error => {
            console.log(error)
            this.notdone = true
            this.loading = false
          })
      this.SaveQuize = false
      this.$root.$emit('getCourses')
    },




  }
}
</script>

