<template>
    <v-container>
        <v-sheet
        class="mx-auto mod2"
        elevation="8"
        max-width="1130"
        
      >
     <header style="font-size: x-large; font-weight: 500; color: #A02520;">Your Hosting Plans</header>
        <v-slide-group
          v-model="model"
          class="pa-4"
          show-arrows
          
        >
          <v-slide-item
            v-for="item in packages"
            :key="item.id"
            v-slot="{ active, toggle }"
            class="mod"
          >
            <v-card
              :color="active ? 'primary' : 'white lighten-1'"
              v-on:click="acti = item.usage"
              class="ma-4 mod"
              height="230"
              width="300"
              @click="toggle"
              elevation="8"
            >
                 <v-card-subtitle style="font-size:40px;">{{ item.name }}</v-card-subtitle>
                <v-img
                  max-width="150px"
                  :src="require('@/assets/img/' + item.name + '.png')"
                  style="left: 25%; "
                >
             
                </v-img>
               <v-card-subtitle></v-card-subtitle>
          
           
          
            </v-card>
          </v-slide-item>
        </v-slide-group>
<v-divider></v-divider>
          <v-sheet
            
            class="mod"
          >
        <v-row style="margin-top: 2%;">
            <v-col>
              <apexchart  type="donut" :options="options" :series="[100-acti,acti]" ></apexchart>
              <v-btn
      color="secondary"
      elevation="2"
      large>reactivate</v-btn>
              <v-btn>upgrade</v-btn>
            </v-col>
            <v-col><calender></calender></v-col>
          </v-row>
          </v-sheet>
      </v-sheet>
    </v-container>
</template>
<script>
import calender from './calender.vue'

export default {
  components: { calender },
    data: () => ({

        options: {
            labels: ['free space', 'used space'],
            colors: ['#494443','#F44336'],

            plotOptions: {
                pie: {
                    expandOnClick: false
                }
            }
        },
      
        acti: 0,
        model: null,
        packages: [{ id: 0, name: "Student", usage: 66,time_left:'30day' , time_left: 6 },
        { id: 1, name: "Bronze",usage:23 , time_left: 1 },
        { id: 2, name: "Silver", usage: 100 , time_left: 26 },
            { id: 3, name: "Gold", usage: 82 ,time_left:22}],
        
        sendData: {
            Package_Name: '',
            space: '',
            Bandwidth: '',
            Email_Accounts: '',
            MySQL_Accounts: '',
            php_enable: '',
            ssl_certificate: '',
            Duration: '',
            Yearly_Price: '',
            Yearly_price_Outside_Syria: ''

        }



    }),
    methods: {

        savePackage() {
            this.loading = true,
                axios
                    .post(
                        '/api/questionAdd',
                        {
                            package_type: this.sendData.Package_Name,
                            space: this.sendData.space,
                            bandwidth: this.sendData.Bandwidth,
                            email_accounts: this.sendData.Email_Accounts,
                            mysql_accounts: this.sendData.MySQL_Accounts,
                            php_enable: this.sendData.php_enable,
                            ssl_certificate: this.sendData.ssl_certificate,
                            duration: this.sendData.Duration,
                            yearly_price: this.sendData.Yearly_Price,
                            yearly_price_outside_syria: this.sendData.Yearly_price_Outside_Syria,
                            price: this.sendData.Yearly_Price,
                            available: true
                        },
                        {
                            headers: {
                                'Content-Type': `multipart/form-data`
                            }
                        }
                    )
                    .then(res => {
                        console.log(res)
                        this.loading = false
                        this.done = true
                    })
                    .catch(error => {
                        console.log(error)
                        this.notdone = true
                        this.loading = false
                    })
            this.SaveQuize = false
            this.$root.$emit('getCourses')
        },

   



    },
    computed: {
        usage_chart() {
            this.series.push(this.packages[this.acti].usage)
            this.series.push(100-this.packages[this.acti].usage)
        }
    },
    comments: {
        calender
    }
}
</script>

<style>
.v-card--link:before {
     background: white;}
.mod  {
    border-top-right-radius: 10px !important;
    border-bottom-right-radius: 60px !important;
    border-top-left-radius: 60px !important;
    border-bottom-left-radius: 10px !important;
}
.rev_mod  {
    border-top-right-radius: 60px !important;
    border-bottom-right-radius: 10px !important;
    border-top-left-radius: 10px !important;
    border-bottom-left-radius: 60px !important;
}
.mod2  {
    border-top-right-radius: 60px !important;
    border-bottom-right-radius: 0px !important;
    border-top-left-radius: 60px !important;
    border-bottom-left-radius: 0px !important;
    text-align: center;
}

</style>