<template>
    <v-row class="fill-height">
        <v-col>
            <v-sheet height="64">
                <v-toolbar flat>
                    <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
                        Today
                    </v-btn>
                    <v-btn fab text small color="grey darken-2" @click="prev">
                        <v-icon small>
                            mdi-chevron-left
                        </v-icon>
                    </v-btn>
                    <v-btn fab text small color="grey darken-2" @click="next">
                        <v-icon small>
                            mdi-chevron-right
                        </v-icon>
                    </v-btn>
                    <v-toolbar-title v-if="$refs.calendar">
                        {{ $refs.calendar.title }}
                    </v-toolbar-title>
                     <v-spacer></v-spacer>
                      <v-toolbar-title >
                           Hosting plan
                        </v-toolbar-title>
                </v-toolbar>
            </v-sheet>
            <v-sheet height="550">
                <v-calendar ref="calendar" v-model="focus" color="primary" :events="events" :event-color="getEventColor"
                    :type="type" 
                    ></v-calendar>
                
            </v-sheet>
        </v-col>
    </v-row>
</template>

<script>
export default {
    data: () => ({
        focus: '',
        type: 'month',
       
        selectedEvent: {},
        selectedElement: null,
        selectedOpen: false,
        events: [],
        colors: ['blue', 'brown', '#bdbdbe', 'yellow', 'green', 'orange', 'grey darken-1'],
        packages: [
        { id: 0, name: "Student", usage: 66, start: '2023-05-20', end: '2023-05-30' },
        { id: 1, name: "Bronze", usage: 23 ,start: '2023-05-01', end: '2023-05-05' },
        { id: 2, name: "Silver", usage: 100 , start: '2023-05-09', end: '2023-05-13' },
        { id: 3, name: "Gold", usage: 82 , start: '2023-05-19', end: '2023-06-05' }],
    }),
    mounted() {
        this.$refs.calendar.checkChange()
    },
    methods: {
        viewDay({ date }) {
            this.focus = date
            this.type = 'day'
        },
        getEventColor(event) {
            return event.color
        },
        setToday() {
            this.focus = ''
        },
        prev() {
            this.$refs.calendar.prev()
        },
        next() {
            this.$refs.calendar.next()
        },
      
       
    },
    created() {
        {
            for (let i = 0; i < this.packages.length; i++) {
                this.events.push({
                    name: this.packages[i].name,
                    start: this.packages[i].start,
                    end: this.packages[i].end,
                    color: this.colors[i],
                })
            }
            this.events = events

        }

    }
}
</script>

