<template>
    <v-container>
        <v-card>
            <v-container>
                <div>
        <v-btn
            text
            color="primary"
          @click="v0=!v0"
        >New Support Ticket</v-btn>
        <v-banner
          v-model="v0"
          single-line
          transition="slide-y-transition"
        >
           <v-textarea
                placeholder="type your message"
                outlined
                rows="5"
                row-height="50"
                shaped
            ></v-textarea>
          <template v-slot:actions="{ dismiss }" >
            <v-btn
              text
              color="primary"
              @click="dismiss"
            >
              close
            </v-btn>
            <v-btn
              
              color="primary"
            >
              send
            </v-btn>
          </template>
        </v-banner>
      </div>
            </v-container>
            <div v-show="items != null">
        
                 <v-menu
          transition="slide-x-transition"
          bottom
          right
        >
          <template v-slot:activator="{ on, attrs }">
            
            <v-btn
             v-bind="attrs"
                  v-on="on"
                    dark
                    icon
                  >
                    <v-icon color="primary" >mdi-dots-vertical</v-icon></v-btn>
         
          </template>

          <v-list>
            <v-list-item>
              <v-list-item><v-btn text color="primary">Stop the Ticket</v-btn></v-list-item>
            </v-list-item>
          </v-list>
        </v-menu>
           

          <v-list>
            <template v-for="(item) in items">
              <v-list-item
                v-if="item.action"
                :key="item.title"
                
              >
               
    <v-spacer v-if="item.sender== 'customer'"></v-spacer>

                <v-list-item-content>
                  <v-card
        :class="changeClass(item.sender)"
        color="grey"
        dark
        max-width="550"
        style="margin-right: 0%;"
      >
      
        <v-card-text class="text-h5 font-weight-bold">
          "{{ item.message }}"
        </v-card-text>

        <v-card-actions>
          <v-list-item class="grow">
            <v-list-item-avatar color="secondary" >
              <v-img
                class="elevation-6"
                alt=""
                src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                v-if="item.sender == 'operator'"
                ></v-img>
                <v-icon
                    class="elevation-6"
                    v-else
                    >fa-user</v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>{{item.sender}}</v-list-item-title>
            </v-list-item-content>

            <v-row
              align="center"
              justify="end"
            >
            <v-icon class="mr-1">
                        mdi-calendar
                      </v-icon>
                      <span class="subheading mr-2">2/30</span>
              <v-icon class="mr-1">
                mdi-clock
              </v-icon>
              <span class="subheading mr-2">2:25</span>
              
            </v-row>
          </v-list-item>
        </v-card-actions>
      </v-card>
                </v-list-item-content>
              </v-list-item>
            </template>
          </v-list>
        </div>
                </v-card>

    </v-container>
</template>
<script>
export default {
    data: () => ({
        v0: false,
        inset: true,
        items: [
            {
                action: 'mdi-label',
                title: 'List item 1',
                sender: 'customer',
                message: "Turns out semicolon-less style is easier and safer in TS because most gotcha edge cases are type invalid as well."
            },
            {
                divider: true,
            },
            {
                action: 'mdi-label',
                title: 'List item 2',
                sender: 'operator',
                message: "Turns out semicolon-less style is easier and safer in TS because4a edge cases are type invalid as well."

            },
            {
                divider: true,
            },
            {
                action: 'mdi-label',
                title: 'List item 3',
                sender: 'customer',
                message: "TS because most gotcha edge cases are type invalid as well."

            },
        ],
    }),
    methods: {

        savePackage() {
            this.loading = true,
                axios
                    .post(
                        '/api/questionAdd',
                        {
                            package_type: this.sendData.Package_Name,
                            space: this.sendData.space,
                            bandwidth: this.sendData.Bandwidth,
                            email_accounts: this.sendData.Email_Accounts,
                            mysql_accounts: this.sendData.MySQL_Accounts,
                            php_enable: this.sendData.php_enable,
                            ssl_certificate: this.sendData.ssl_certificate,
                            duration: this.sendData.Duration,
                            yearly_price: this.sendData.Yearly_Price,
                            yearly_price_outside_syria: this.sendData.Yearly_price_Outside_Syria,
                            price: this.sendData.Yearly_Price,
                            available: true
                        },
                        {
                            headers: {
                                'Content-Type': `multipart/form-data`
                            }
                        }
                    )
                    .then(res => {
                        console.log(res)
                        this.loading = false
                        this.done = true
                    })
                    .catch(error => {
                        console.log(error)
                        this.notdone = true
                        this.loading = false
                    })
            this.SaveQuize = false
            this.$root.$emit('getCourses')
        },


        changeClass(value) {
            return {
                'rev_mod': value == 'customer',
                'mod': value == 'operator',

            }

        }

    }
}
</script>

<style></style>